/* pallete */
:root{
    --primay : #62A87C  ;
    --white  : #F1F2F6;
    --bold : #191919;
    --secondry : #E84855;
    --grad : #32373B;

    --shadow : #ccc;


}

body{
    background-color: var(--white);
    color: var(--bold);
}



.no-link{
    text-decoration: none;
    color : var(--bold);
}


.to-right{
    margin-left: auto !important;
}

.container{
    padding-top: 56px;
}




/* navbar */
.navbar{
    position: fixed;

    height: 56px;
    width:100%;
    background-color: var(--white);
    /* border-bottom: 1px solid black; */

    display: flex;
    align-items: center;
    box-shadow: 0 0 15px var(--shadow);

    z-index: 10;
}


.title{
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;

    font-weight: 800;
    color: var(--primay);
    text-transform: uppercase;

    padding: 10px;
}

.btn{
    background-color: var(--primay);
    padding: 8px;
    border-radius: 5px;
    font-size: 18px;
    color: var(--white);
    margin: 8px;
    cursor: pointer;

    text-transform: uppercase;
    font-weight: 600;
}

.yellow{
    background-color: var(--secondry);
}

.username{
    font-size: 18px;
    color: var(--bold);
    text-transform: capitalize;
}


/* page */
.page-controller{
    display: flex;
    align-items: center;
    justify-content: center;
}

.page{
    font-size: 32px;
    
}

/* ============================== */
/* events */

.event-holder{
    display: grid;
    grid-template-columns : repeat(3 , 1fr);
    grid-auto-rows: 400px;
    margin: 0 15%;
}

.event-image{
    height: 50%;
    overflow: hidden;
    object-fit: cover;
    width: 100%;
}

.event-card{
    margin: 8px;
    box-shadow: 0 0 16px var(--shadow);
    background-color: var(--white);
    border-radius: 5px;
    /* padding: 16px; */
}

.event-text{
    padding:0 16px;
}

.event-desc{
    font-size: 18px;
    font-weight: 400;
    color: var(--grad)
}

